import { Injectable } from '@angular/core';
import { IOperation, OperationService, OperationStatus } from '@c8y/client';
import { AlertService, gettext, ModalService, Status } from '@c8y/ngx-components';
import { assign, difference, has, includes, keys, pickBy, without } from 'lodash-es';
import { RESERVED_KEYS, STANDARD_KEYS } from './operations.model';

@Injectable()
export class OperationsService {
  constructor(
    private operationService: OperationService,
    private modal: ModalService,
    private alertService: AlertService
  ) {}

  getStandardKeys(operation: IOperation) {
    return pickBy(STANDARD_KEYS, (_, key) => has(operation, key));
  }

  getNonStandardKeys(operation: IOperation, excluding: string[] = []) {
    return without(
      difference(this.getKeys(operation), keys(this.getStandardKeys(operation))),
      ...excluding
    );
  }

  async cancel(operation: Partial<IOperation>) {
    try {
      await this.modal.confirm(
        gettext('Cancel operation'),
        gettext('You are about to cancel the operation. Do you want to proceed?'),
        Status.DANGER
      );

      const operationAfterUpdate = (
        await this.operationService.update({
          id: operation.id,
          status: OperationStatus.FAILED,
          failureReason: gettext('Operation cancelled by user.')
        })
      ).data;
      assign(operation, operationAfterUpdate);
      this.alertService.success(gettext('Operation cancelled.'));
    } catch (er) {
      if (er) {
        this.alertService.addServerFailure(er);
      }
    }
  }

  private getKeys(managedObject) {
    return Object.keys({ ...managedObject }).filter(key => !includes(RESERVED_KEYS, key));
  }
}
